/* Contact.css */

/* Reset styles */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Body styles */
  body {
    background: #eaeef6;
    font-family: 'Open Sans', sans-serif;
  }
  
  /* Contact page styles */
  .contact {
    margin-top: 80px; /* Adjust based on your header height */
    padding: 2rem;
    text-align: center;
  }
  
  .contact__content {
    background-color: #fff;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  .contact h1 {
    color: #406ff3;
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .contact p {
    color: #6a778e;
    font-size: 1.2rem;
    line-height: 1.6;
  }
  