/* Navbar.css */

/* Reset styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body styles */
body {
  background: #eaeef6;
  font-family: 'Open Sans', sans-serif;
}

/* Header styles */
.header {
  background: #fff;
  padding: 1rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

/* Navbar styles */
.navbar {
  display: flex;
  justify-content: center;
}

.navbar__menu {
  list-style: none;
  display: flex;
}

.navbar__item {
  position: relative;
  margin: 0 1rem;
  padding: 0.5rem;
  transition: all 0.3s ease;
}

.navbar__item:hover {
  transform: translateY(-3px);
}

.navbar__link {
  position: relative;
  display: flex;
  align-items: center;
  color: #6a778e;
  text-decoration: none;
  transition: all 0.3s ease;
}

.navbar__link i {
  margin-right: 0.25rem; /* Adjust the space between icon and text */
}

.navbar__link:hover {
  color: #406ff3;
}

.navbar__link::after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #406ff3;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.3s ease;
}

.navbar__link:hover::after {
  transform: scaleX(1);
}
